import React, { useState, useContext, useEffect } from 'react'
import "./Body.css"
import Loader from "./Loader"
import { SetCountry } from "./Home";

export default function Body() {
    const [confirmed, setConfirmed] = useState("");
    const [recovered, setRecovered] = useState("");
    const [deaths, setDeaths] = useState("");
    const getCountry = useContext(SetCountry);
    useEffect(() => {
        let c = getCountry.split("(");
        FetchCovidData(c[0]);
        async function FetchCovidData(country) {
            const apiKey = "6cc5c51788mshd0c62b2be206fbdp1fd87cjsn5f77e0d4cb1d";
            const covidApi = `https://covid-19-data.p.rapidapi.com/country?name=${country}`;
            const response = await fetch(covidApi, {
                headers: {
                    'x-rapidapi-key': apiKey,
                },
            })
            let data = await response.json();
            if (data[0] != null) {
                setConfirmed(data[0].confirmed)
                setRecovered(data[0].recovered)
                setDeaths(data[0].deaths)
            } else {
                setConfirmed("No Cases")
                setRecovered("No Cases")
                setDeaths("No Cases")
            }
        }
    }, [getCountry])

    return (
        <div className="body">
            <div className="confirmed">
                <h1>{confirmed !== "" ? confirmed : <Loader />}</h1>
                <p><strong>Confirmed</strong> </p>
            </div>
            <div className="recovered">
                <h1>{recovered !== "" ? recovered : <Loader />}</h1>
                <p> <strong>Recovered</strong> </p>
            </div>
            <div className="deaths">
                <h1>{deaths !== "" ? deaths : <Loader />}</h1>
                <p> <strong>Deaths</strong></p>
            </div>
        </div>

    )
}
